.contacto-page {
  text-align: center;
  padding-top: 100px;
  color: white;
  min-height: 100vh;
  position: relative;
  background-color: #2dd2c3;
}

.contacto-page h1 {
  font-size: 2.2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.contacto-page p {
  font-size: 1.1rem;
  margin-bottom: 2rem;
}

.map-container {
  max-width: 90%;
  margin: 0 auto;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.map-container iframe {
  width: 100%;
  height: 350px;
  border: none;
  display: block;
}

.footer-button {
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 4rem;
}

.back-button {
  padding: 12px 24px;
  background-color: white;
  color: #00838f;
  font-weight: bold;
  border-radius: 30px;
  text-decoration: none;
  display: inline-block;
  transition: background 0.3s ease;
}

.back-button:hover {
  background-color: #e0f0f0;
}

/* Optional: bubbles */
.bubbles {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  overflow: hidden;
  pointer-events: none;
}

.bubble {
  position: absolute;
  bottom: -100px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  animation: rise infinite;
  z-index: 0;
}

@keyframes rise {
  to {
    transform: translateY(-120vh);
  }
}

.contacto-page > *:not(.bubbles) {
  position: relative;
  z-index: 1;
}

.hero-image-image {
  width: 100%;
  height: 200px;
  background-size: cover;
  background-position: center;
  margin-bottom: 2rem;
  border-radius: 15px;
}

